.arrow-1,.arrow-2 {
	fill: none;
	stroke: #798992;
	stroke-linecap: round;
	stroke-linejoin: round; }
.arrow-1 {
	stroke-width: 7px; }
.arrow-2 {
	stroke-width: 6px; }

.calendar-1,.calendar-2 {
	fill: none;
	stroke: #798992;
	stroke-linejoin: round;
	stroke-width: 5.88px; }
.calendar-2 {
	stroke-linecap: square; }

