.navbar__button {
	//border: 1px solid red
	width: 3rem;
	height: 3rem;
	margin-top: .5rem; }
.logo {
	width: 110px;
	//height: 3rem
 }	//margin-top: .5rem
#logo {
	#Tag {
		display: none; } }
.closeMenu {
	margin-top: .5rem;
	width: 3rem;
	height: 3rem; }

.nav {
	//background: lighten($gray-500, 40%)
	z-index: 9999;
	height: 4rem;
	line-height: 4rem;
	a.startButton {
		color: $white;
		background: lighten($gray-500, 40%); } }
#sidebar.floatMenu {
	background: darken($primary, 0%);
	color: $white;
	ul.listNav {
		&> li {
			width: 100%;
			border-top: 1px solid darken($red, 10%);
			padding: .8rem 0; }
		li:first-child {
			border-top: none; }
		li {
			font-weight: 800;
			a {
				color: $white; }
			li {
				font-weight: 400;
				padding: .5rem 0;
				a {
					//height: 8rem
					//line-height: 2.5rem
					color: $white; } } } } }
.i-amphtml-sidebar-mask {
	opacity: 1;
	background-color: rgba($white, .8); }


